.weight {
  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    padding: 83px 110px 71px;
  }
}

.weight__wrapper {
  width: 280px;
  color: @base-font-color;
  background-color: @block-background-color;
  padding: 18px 20px;
  margin-bottom: 20px;

  &--gain .weight__title {
    padding-left: 78px;

    &::after {
      content: url("../img/index/cat-pro.svg");
      height: 50px;
      width: 36px;
      left: -9px;
      top: 5px;
    }
  }

  @media (min-width: @tablet-width) {
    width: 668px;
  }

  @media (min-width: @desktop-width) {
    width: 530px;
    margin-right: 80px;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }
}

.weight__title {
  font-family: @font;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
  color: @black-color;
  margin-bottom: 29px;
  padding-left: 69px;
  position: relative;

  &::before {
    content: "";
    display: inline-block;
    height: 50px;
    width: 50px;
    background-color: @decorate-background-color;
    border-radius: 50%;
    position: absolute;
    left: 0;
    bottom: -7px;
  }

  &::after {
    content: url("../img/index/cat-slim.svg");
    height: 50px;
    width: 36px;
    position: absolute;
    left: 10px;
    bottom: -6px;
  }
}

.weight__link-catalog {
  display: inline-block;
  text-decoration: none;
  color: @black-color;
  margin-top: 38px;
}

.weight__name-catalog {
  font-family: @font;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
}
